
.am-date-picker {
  position: relative;
  width: 100%;
}
.am-datepicker-dropdown {
  padding: 0 !important;
  display: block;
  position: absolute;
  top: 100%;
  -webkit-animation-duration: .15s;
          animation-duration: .15s;
}
.am-date-picker-btn {
  width: 100%;
  padding-left: 10px;
  text-align: right;
  background: white;
  border-color: #ccc;
}
.am-selected-placeholder,
.am-date-picker-status {
  width: auto;
  white-space: normal;
  text-align: left;
  display: block;
  word-wrap: normal;
  overflow: hidden;
  min-height: 19px;
}
.am-selected-placeholder {
  opacity: .65;
}
.am-datepicker-dropdown:before {
  left: 10px;
  top: -8px;
  pointer-events: none;
  bottom: 0;
  border-bottom-style: solid;
  position: absolute;
  display: block;
  content: "";
  width: 0;
  height: 0;
  border: 8px dashed transparent;
  border-width: 0 8px 8px 8px;
  border-bottom-color: #3bb4f2;
  z-index: 1;
}
.am-datepicker-dropdown.am-datepicker-success:before {
  border-bottom-color: #5eb95e;
}
.am-datepicker-dropdown.am-datepicker-warning:before {
  border-bottom-color: #F37B1D;
}
.am-datepicker-dropdown.am-datepicker-danger:before {
  border-bottom-color: #dd514c;
}
.am-date-picker.am-active .am-datepicker-dropdown {
  display: block;
}



